import { useState } from "react";

export const Carousel = () => {
  const slides = [
    "/images/slide1.jpg",
    "/images/slide2.png",
    "/images/slide3.png",
  ];
  const [current, setCurrent] = useState(0);
  const len = slides.length;

  const prev = () => setCurrent((current - 1 + len) % len);
  const next = () => setCurrent((current + 1) % len);

  return (
    <div
      style={{
        position: "relative",
        width: "100%",
        maxWidth: "600px",
        margin: "0 auto",
      }}
    >
      {/* Imagen actual */}
      <div style={{ overflow: "hidden", height: "300px" }}>
        <img
          src={slides[current]}
          alt={`Slide ${current + 1}`}
          style={{
            width: "100%",
            height: "100%",
            objectFit: "cover",
          }}
        />
      </div>

      {/* Botones */}
      <button
        onClick={prev}
        style={{
          position: "absolute",
          top: "50%",
          left: "16px",
          transform: "translateY(-50%)",
          background: "rgba(0,0,0,0.5)",
          color: "white",
          border: "none",
          borderRadius: "50%",
          width: "32px",
          height: "32px",
          cursor: "pointer",
        }}
      >
        ‹
      </button>
      <button
        onClick={next}
        style={{
          position: "absolute",
          top: "50%",
          right: "16px",
          transform: "translateY(-50%)",
          background: "rgba(0,0,0,0.5)",
          color: "white",
          border: "none",
          borderRadius: "50%",
          width: "32px",
          height: "32px",
          cursor: "pointer",
        }}
      >
        ›
      </button>
    </div>
  );
};
